import React, {ComponentType} from 'react';
import {connect} from 'react-redux';

import {RootState} from '../../redux/storeRedux';
import {
    followUser,
    setUpAllUsers,
    setUpCurrentPage,
    toggle_followingInProgress,
    toggle_isFetching,
    unfollowUser
} from '../../redux/usersReducer';
import {UsersAPIComponent} from './UsersAPIComponent';
import {compose} from 'redux';
import {withAuthRedirect} from '../HOCs/withAuth';
import {
    getUsers,
    getTotalUsersCount,
    getPageSize,
    getCurrentPage,
    getIsFetchingUsers,
    getFriendsList
} from '../util/reduxSelector';


const mapStateToProps = (state: RootState) => {
    return {
        users: getUsers(state),
        totalCount: getTotalUsersCount(state),
        pageSize: getPageSize(state),
        currentPage: getCurrentPage(state),
        isFetching: getIsFetchingUsers(state),
        followingInProgress: getFriendsList(state),

    }
}


export const UsersContainer = compose<ComponentType>(withAuthRedirect, connect(mapStateToProps, {
    toggle_isFetching, toggle_followingInProgress,
    setUpAllUsers, setUpCurrentPage,
    followUser, unfollowUser
}))(UsersAPIComponent)





