import {ActionsTypes} from './state';
import {v1} from 'uuid';
import {Dispatch} from 'redux';
import {UsersAPI} from '../api/socialNetworkAPI';


const FOLLOWString = 'USERS/USERS_PAGE/FOLLOW';
const UNFOLLOWString = 'USERS/USERS_PAGE/UNFOLLOW';
const SET_USERS = 'USERS/USERS_PAGE/SET_USERS';
const SET_USERS_TOTAL_COUNT = 'USERS/USERS_PAGE/USERS_TOTAL_COUNT';
const SET_USERS_CURRENT_PAGE = 'USERS/USERS_PAGE/USERS_CURRENT_PAGE';
const IS_FETCHING_USERS_LIST = 'USERS/USERS_PAGE/IS_FETCHING_USERS_LIST';
const TOGGLE_FOLLOWING_IN_PROGRESS = 'USERS/USERS_PAGE/TOGGLE_FOLLOWING_IN_PROGRESS';

export const follow_userSUCCESS = (userID: string | number) => {
    return {
        type: FOLLOWString,
        userID: userID,
    } as const
};

export const unfollow_userSUCCESS = (userID: string | number) => ({
    type: UNFOLLOWString,
    userID: userID,
} as const);


export const set_users = (users: Array<UsersType>) => ({
    type: SET_USERS,
    users: users,
} as const)


export const set_users_total_count = (totalCount: number) => ({
    type: SET_USERS_TOTAL_COUNT,
    totalCount: totalCount,
} as const)

export const set_users_currentPageSUCCESS = (currentPage: number) => ({
    type: SET_USERS_CURRENT_PAGE,
    currentPage,
} as const)

export const toggle_isFetching = (isFetching: boolean) => ({
    type: IS_FETCHING_USERS_LIST,
    isFetching,
} as const)

export const toggle_followingInProgress = (isFetching: boolean, userId: string) => ({
    type: TOGGLE_FOLLOWING_IN_PROGRESS,
    isFetching,
    userId,
} as const)


type UsersLocationType = {
    city: string
    country: string
}

export type UsersType = {
    followed: boolean
    id: string
    name: string
    photos: {
        large: string | null
        small: string | null
    }
    status: string | null
    uniqueUrlName: string | null
}


// export type UsersPagePropsType = {
//     users: Array<UsersType>
// }

export type UsersPagePropsType = typeof usersInitialState


const usersInitialState = {
    users: [] as Array<UsersType>,
    totalCount: 0,
    pageSize: 5,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [] as Array<string>,
}


export const usersReducer = (state = usersInitialState, action: ActionsTypes): UsersPagePropsType => {
    switch (action.type) {
        case FOLLOWString: {
            return {
                ...state,
                users: state.users.map((u) => {
                    if (u.id === action.userID) {
                        return {
                            ...u,
                            followed: false
                        }
                    }
                    return u
                })
            }
        }
        case UNFOLLOWString: {

            return {
                ...state,
                users: state.users.map(u => {
                    if (u.id === action.userID) {
                        return {
                            ...u,
                            followed: true
                        }
                    }
                    return u
                })
            }
        }
        case SET_USERS : {
            return {
                ...state,
                users: action.users
            }
        }
        case SET_USERS_TOTAL_COUNT: {
            return {
                ...state,
                totalCount: action.totalCount
            }
        }
        case SET_USERS_CURRENT_PAGE: {
            return {
                ...state,
                currentPage: action.currentPage
            }
        }
        case IS_FETCHING_USERS_LIST: {
            return {
                ...state,
                isFetching: action.isFetching
            }
        }
        case TOGGLE_FOLLOWING_IN_PROGRESS: {

            let stateCopy = {
                ...state,
                followingInProgress: action.isFetching ? [...state.followingInProgress, action.userId] : state.followingInProgress.filter(id => id != action.userId)
            }
            return stateCopy
        }


    }
    return state;
}


export const setUpAllUsers = (pageSize: number, currentPage: number) => (dispatch: Dispatch) => {

    dispatch(toggle_isFetching(true))
    UsersAPI.getUsers(pageSize, currentPage).then((data) => {
        dispatch(set_users_total_count(data.totalCount))
        dispatch(set_users(data.items))
        dispatch(toggle_isFetching(false))

    })
}

export const setUpCurrentPage = (pageSize: number, currentPage: number, pageNumber: number) => (dispatch: Dispatch) => {

    dispatch(toggle_isFetching(true))
    dispatch(set_users_currentPageSUCCESS(pageNumber))
    UsersAPI.getUsers(pageSize, currentPage).then((data) => {
        dispatch(set_users_total_count(data.totalCount))
        dispatch(set_users(data.items))
        dispatch(toggle_isFetching(false))
    })
}


export const followUser = (userId: string) => (dispatch: Dispatch) => {
    dispatch(toggle_followingInProgress(true, userId,))
    UsersAPI.foolowUser(userId, {}).then((data) => {
        if (data.resultCode == 0) {
            dispatch(unfollow_userSUCCESS(userId))
            dispatch(toggle_followingInProgress(false, userId,))
        }
    })
};

export const unfollowUser = (userId: string) => (dispatch: Dispatch) => {
    dispatch(toggle_followingInProgress(true, userId,))
    UsersAPI.unfollowUser(userId).then((data) => {
        if (data.resultCode == 0) {
            dispatch(follow_userSUCCESS(userId))
            dispatch(toggle_followingInProgress(false, userId,))
        }
    })
}