import React, {ChangeEvent} from "react";

import {AddPostAC, ProfileReducer, ShowPostTextInTextareaAC} from '../../../redux/profileReducer';
import MyPosts from './MyPosts';
import { RootState} from '../../../redux/storeRedux';
import {connect} from 'react-redux';
import {PostType} from './Post/Post';
import {DispatchType} from '../../../redux/state';

type mapStateToPropsReturnType = {
    textAreaState: string
    posts: Array<PostType>
}
type mapDispatchToPropsReturnType = {
    showTextInTextarea: (e: ChangeEvent<HTMLTextAreaElement>)=>void
    addNewPost:(newPost: string)=>void
}

const mapStateToProps = (state:RootState) => ({
    textAreaState: state.profilePage.textAreaForUserStatus,
    posts:state.profilePage.posts
})
const mapDispatchToProps = (dispatch:DispatchType):mapDispatchToPropsReturnType => ({
    addNewPost: (newPost: string ) => dispatch(AddPostAC(newPost)),
    showTextInTextarea: (e: ChangeEvent<HTMLTextAreaElement>) => dispatch(ShowPostTextInTextareaAC(e.currentTarget.value))
})

export const MyPostsContainer=connect(mapStateToProps,mapDispatchToProps)(MyPosts);
