import React, {ChangeEvent, ComponentType} from 'react';
import {MessageType} from './Message/Message';
import {DialogWithFriend} from './Dialog/Dialog';
import {SendMessageOrderAC, ShowMessageInTextareaAC} from '../../redux/dialogsReducer';
import {Dialogs, DialogsType2} from './Dialogs';
import {MyContext} from '../../redux/MyContext';
import {connect} from 'react-redux';
import {RootState} from '../../redux/storeRedux';
import {DispatchType} from '../../redux/state';
import {ShowPostTextInTextareaAC} from '../../redux/profileReducer';
import {PostType} from '../Profile/MyPosts/Post/Post';
import {UsersAPIComponent} from '../Users/UsersAPIComponent';
import {withAuthRedirect} from '../HOCs/withAuth';
import {compose} from 'redux';
import {getDialoguesList, getMessagesList} from '../util/reduxSelector';


export type DialogsPagePropsType = {
    dialogs: Array<DialogWithFriend>
    messages: Array<MessageType>
}


type mapStateToPropsReturnType = {
    dialoguesList: DialogWithFriend[]
    messagesList: MessageType[]
}
type mapDispatchToPropsReturnType = {
    sendMessage: (message: string) => void
    onChange: (e: ChangeEvent<HTMLTextAreaElement>) => void
}

const mapStateToProps = (state: RootState): mapStateToPropsReturnType => ({
    dialoguesList: getDialoguesList(state),
    messagesList: getMessagesList(state),
})
const mapDispatchToProps = (dispatch: DispatchType): mapDispatchToPropsReturnType => ({
    sendMessage: ( message: string) => dispatch(SendMessageOrderAC(message)),
    onChange: (e: ChangeEvent<HTMLTextAreaElement>) => dispatch(ShowMessageInTextareaAC(e.currentTarget.value))
})




export const DialogsContainer = compose <ComponentType>(
    connect<mapStateToPropsReturnType, mapDispatchToPropsReturnType, DialogsType2, RootState>(mapStateToProps, mapDispatchToProps),
    withAuthRedirect,
)(Dialogs)